import React, { useState, useEffect, useRef } from "react";
import axios from "axios";
import { Box, TextField, Button, Typography, Divider } from "@mui/material";
import { chatChatGpt } from "../api/mapApi";

const ChatPage = () => {
  const [messages, setMessages] = useState([]); //   
  const [input, setInput] = useState(""); //  
  const [loading, setLoading] = useState(false); //  
  const messagesEndRef = useRef(null); //    ref

  //       
  useEffect(() => {
    if (messagesEndRef.current) {
      messagesEndRef.current.scrollIntoView({ behavior: "smooth" });
    }
  }, [messages]);

  const handleSend = async () => {
    if (input.trim() === "") return;

    const newMessages = [...messages, { sender: "user", text: input }];
    setMessages(newMessages);
    setInput(""); //   
    setLoading(true); //  

    let obj = {
      query: input,
    };

    try {
      const res = await chatChatGpt(obj);
      setMessages((prevMessages) => [
        ...prevMessages,
        { sender: "bot", text: res.data },
      ]);
    } catch (e) {
      console.error("Error calling the chat API:", e);
      setMessages((prevMessages) => [
        ...prevMessages,
        { sender: "bot", text: " .  ." },
      ]);
    } finally {
      setLoading(false); //  
    }
  };

  return (
    <>
      <div className="header-box">
        <div>AI   </div>
      </div>
      <div className="chat-box">
        <div className="message-box">
          {messages.length === 0 && (
            <Typography
              variant="body2"
              sx={{
                color: "#888888",
                textAlign: "center",
                marginTop: "20px",
                fontStyle: "italic",
              }}
            >
               ! (: "    ")
            </Typography>
          )}
          {messages.map((msg, index) => (
            <Box
              key={index}
              sx={{
                display: "flex",
                flexDirection: msg.sender === "user" ? "row-reverse" : "row",
                marginBottom: "12px",
              }}
            >
              <Typography
                variant="body1"
                sx={{
                  padding: "12px 16px",
                  backgroundColor:
                    msg.sender === "user" ? "#e5e0ff" : "#f1f1f1",
                  color: msg.sender === "user" ? "#000" : "#333333",
                  borderRadius: "16px",
                  maxWidth: "70%",
                  wordBreak: "break-word",
                  boxShadow: "0px 3px 6px rgba(0, 0, 0, 0.1)",
                  textAlign: "left",
                  whiteSpace: "pre-wrap",
                }}
              >
                {msg.text}
              </Typography>
            </Box>
          ))}

          {/*    */}
          {loading && (
            <Box sx={{ textAlign: "center", marginTop: "12px" }}>
              <Typography variant="body2" sx={{ color: "#888" }}>
                 ...
              </Typography>
            </Box>
          )}

          {/*     */}
          <div ref={messagesEndRef} />
        </div>

        <Divider sx={{ margin: "8px 0" }} />

        {/*   */}
        <div className="input-field">
          <TextField
            value={input}
            onChange={(e) => setInput(e.target.value)}
            placeholder=":   "
            variant="outlined"
            size="medium"
            sx={{
              flexGrow: 1,
              marginRight: "8px",
              backgroundColor: "#ffffff",
              borderRadius: "4px",
              borderColor: "#e5e0ff",
            }}
            onKeyDown={(e) => {
              if (e.key === "Enter") {
                handleSend();
              }
            }}
          />
          <Button
            variant="contained"
            color="primary"
            onClick={handleSend}
            sx={{
              padding: "12px 16px",
              fontWeight: "bold",
              backgroundColor: "#1976d2",
              "&:hover": { backgroundColor: "#145ca8" },
            }}
          >
            
          </Button>
        </div>
      </div>
    </>
  );
};

export default ChatPage;
