import React, { useEffect, useState } from "react";
import axios from "axios";
import "./Distance.css";
import { createTripRoute } from "../api/tripRouteApi";

const ProfileImageUpload = ({ route }) => {
  const [imageFile, setImageFile] = useState(null); //  
  const [preview, setPreview] = useState(null); //   URL

  //  
  const [formData, setFormData] = useState({
    title: "  ",
    content: "      .",
    isPrivate: false,
    locationInfo: route.paths,
    distances: route.distances,
  });

  useEffect(() => {
    setFormData({
      ...formData,
      distances: route.distances[0],
      locationInfo: makeStringData(),
    });
  }, [route]);

  const makeStringData = () => {
    if (route.paths) {
      const locationString = route.paths
        .map((item) => `${item.lat},${item.lng}`)
        .join(",");

      return locationString;
    }
  };

  //    
  const handleFileChange = (e) => {
    const selectedFile = e.target.files[0];
    setImageFile(selectedFile);
    if (selectedFile) {
      setPreview(URL.createObjectURL(selectedFile));
    }
  };

  //    
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };

  //    
  const handleSubmit = async () => {
    const form = new FormData();

    //    (   )
    if (imageFile) {
      form.append("imageFile", imageFile);
    }

    //   
    Object.keys(formData).forEach((key) => {
      form.append(key, formData[key]);
    });

    createTripRoute(form, localStorage.getItem("userToken"))
      .then((res) => {
        alert(" !");
      })
      .catch((e) => {
        if (e.status === 403) {
          alert(" ");
          window.location = "/login";
        } else {
          console.error(" :", e.response || e.message);
          alert("   .");
        }
      });
  };

  return (
    <div className="distance-container">
      <h1 className="distance-h1">  </h1>
      <input
        type="file"
        className="distance-input"
        accept="image/*"
        onChange={handleFileChange}
      />
      {preview && (
        <div className="distance-preview-container">
          <h2 className="distance-h1"></h2>
          <img src={preview} alt=" " width="200" />
        </div>
      )}
      <div>
        <input
          type="text"
          className="distance-input"
          name="title"
          placeholder=""
          value={formData.title}
          onChange={handleInputChange}
        />
        <textarea
          className="distance-textarea"
          name="content"
          placeholder=""
          value={formData.content}
          onChange={handleInputChange}
        />
        <label className="distance-label">
          <input
            type="checkbox"
            className="distance-input"
            name="isPrivate"
            checked={formData.isPrivate}
            onChange={(e) =>
              setFormData({
                ...formData,
                isPrivate: e.target.checked,
              })
            }
          />
           
        </label>
      </div>
      <button className="distance-button" onClick={handleSubmit}>
        
      </button>
    </div>
  );
};

export default ProfileImageUpload;
